import React from "react";
import { useVentas } from "../context/VentasContext";
import DashboardCharts from "./Charts/DashboardCharts";

export default function VentasModal() {
  const { ventas, enviarVenta, eliminarVenta, setVentaEditando } = useVentas();

  return (
    <div className="modal fade" id="modalVentas" tabIndex="-1" aria-hidden="true">
      <div className="modal-dialog modal-xl modal-dialog-scrollable">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title"><i className="bi bi-bar-chart me-2"></i>Dashboard de Ventas</h5>
            <button type="button" className="btn-close" data-bs-dismiss="modal"></button>
          </div>

          <div className="modal-body">
            <ul className="nav nav-pills mb-3" role="tablist">
              <li className="nav-item">
                <button className="nav-link active" data-bs-toggle="pill" data-bs-target="#pane-tabla">
                  <i className="bi bi-table me-1"></i> Tabla Completa
                </button>
              </li>
              <li className="nav-item">
                <button className="nav-link" data-bs-toggle="pill" data-bs-target="#pane-analytics">
                  <i className="bi bi-graph-up-arrow me-1"></i> Analytics
                </button>
              </li>
            </ul>

            <div className="tab-content">
              {/* Tabla completa */}
              <div className="tab-pane fade show active" id="pane-tabla">
                <div className="table-responsive">
                  <table className="table table-striped table-hover align-middle">
                    <thead>
                      <tr>
                        <th>Número</th><th>Documento</th><th>Nombres</th><th>Correo</th>
                        <th>Curso</th><th>Pago</th><th>Asesor</th><th>Filial</th>
                        <th>País</th><th>Total</th><th>Fecha</th><th>Estado</th><th>Acciones</th>
                      </tr>
                    </thead>
                    <tbody>
                      {ventas.map((v, i) => (
                        <tr key={i}>
                          <td>{v.numeroVenta}</td>
                          <td>{v.dni}</td>
                          <td>{v.nombres}</td>
                          <td>{v.correo}</td>
                          <td>{v.curso}</td>
                          <td>{v.metodoPago}</td>
                          <td>{v.asesor}</td>
                          <td>{v.filial}</td>
                          <td>{v.pais}</td>
                          <td>${v.total}</td>
                          <td>{new Date(v.fechaISO).toLocaleDateString()}</td>
                          <td>
                            <span className={`badge ${
                              v.estado === "Enviado" ? "bg-success" :
                              v.estado === "Pendiente" ? "bg-warning" :
                              "bg-danger"
                            }`}>{v.estado}</span>
                          </td>
                          <td className="d-flex gap-2">
                            {v.estado !== "Enviado" && (
                              <button onClick={() => enviarVenta(i)} className="btn btn-sm btn-success">Enviar</button>
                            )}
                            {v.estado === "Enviado" && (
                              <button className="btn btn-sm btn-secondary" disabled>Enviado</button>
                            )}
                            <button
                              className="btn btn-sm btn-primary"
                              data-bs-toggle="modal"
                              data-bs-target="#modalEditarVenta"
                              onClick={() => setVentaEditando(i)}
                            >
                              Editar
                            </button>
                            <button
                              onClick={() => eliminarVenta(i)}
                              className="btn btn-sm btn-danger"
                            >
                              🗑
                            </button>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              </div>

              {/* Analytics */}
              <div className="tab-pane fade" id="pane-analytics">
                <DashboardCharts ventas={ventas} />
              </div>
            </div>
          </div>

          <div className="modal-footer">
            <span className="small opacity-75 me-auto">Consejo: las métricas se actualizan al instante.</span>
            <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Cerrar</button>
          </div>
        </div>
      </div>
    </div>
  );
}
