import React from "react";
import { VentasProvider } from "./context/VentasContext";
import NuevaVentaForm from "./components/NuevaVentaForm";
import MainContent from "./components/MainContent";
import VentasModal from "./components/VentasModal";
import EditarVentaModal from "./components/EditarVentaModal";
import './index.css';


function App() {
  return (
    <VentasProvider>
      <div className="main-layout">
        <NuevaVentaForm />
        <MainContent />
        <VentasModal />
        <EditarVentaModal />
      </div>
    </VentasProvider>
  );
}

export default App;
