import React, { useState } from "react";
import { Form, Button } from "react-bootstrap";
import { useVentas } from "../context/VentasContext";

export default function NuevaVentaForm() {
  const { agregarVenta } = useVentas();

  const [formData, setFormData] = useState({
    numeroVenta: "",
    dni: "",
    nombres: "",
    correo: "",
    curso: "",
    metodoPago: "",
    asesor: "",
    filial: "",
    pais: "",
    total: "",
    documentos: {
      certificado1: false,
      certificado2: false,
      certificado3: false,
      certificado4: false,
      diploma: false,
    },
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const toggleDocumento = (docKey) => {
    setFormData((prev) => ({
      ...prev,
      documentos: {
        ...prev.documentos,
        [docKey]: !prev.documentos[docKey],
      },
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!formData.numeroVenta || !formData.dni || !formData.nombres) {
      alert("Completa Número de venta, Documento y Nombres");
      return;
    }
    agregarVenta(formData);
    setFormData({
      numeroVenta: "",
      dni: "",
      nombres: "",
      correo: "",
      curso: "",
      metodoPago: "",
      asesor: "",
      filial: "",
      pais: "",
      total: "",
      documentos: {
        certificado1: false,
        certificado2: false,
        certificado3: false,
        certificado4: false,
        diploma: false,
      },
    });
  };

  return (
    <aside className="sidebar p-4 d-flex flex-column">
      <h4 className="mb-3 text-center">Registrar Nueva Venta</h4>
      <Form onSubmit={handleSubmit} className="flex-grow-1 d-flex flex-column">
        <div className="row g-3">
          <div className="col-md-6">
            <Form.Control
              type="text"
              name="numeroVenta"
              placeholder="Teléfono/Celular"
              value={formData.numeroVenta}
              onChange={handleChange}
              required
            />
          </div>
          <div className="col-md-6">
            <Form.Control
              type="number"
              name="total"
              placeholder="Total a pagar"
              value={formData.total}
              onChange={handleChange}
              required
            />
          </div>
          <div className="col-12">
            <Form.Control
              type="text"
              name="nombres"
              placeholder="Nombres completos"
              value={formData.nombres}
              onChange={handleChange}
              required
            />
          </div>
          <div className="col-md-6">
            <Form.Select
              name="tipoDocumento"
              value={formData.tipoDocumento}
              onChange={handleChange}
              required
            >
              <option value="">Tipo de Documento</option>
              <option value="DNI">DNI</option>
              <option value="Cédula">Cédula</option>
              <option value="INE">INE</option>
              <option value="Pasaporte">Pasaporte</option>
            </Form.Select>
          </div>
          <div className="col-md-6">
            <Form.Control
              type="text"
              name="dni"
              placeholder="Número de Documento"
              value={formData.dni}
              onChange={handleChange}
              required
            />
          </div>
          <div className="col-md-12">
            <Form.Control
              type="email"
              name="correo"
              placeholder="Correo electrónico"
              value={formData.correo}
              onChange={handleChange}
            />
          </div>
          <div className="col-12">
            <Form.Control
              type="text"
              name="curso"
              placeholder="Seleccionar curso"
              value={formData.curso}
              onChange={handleChange}
              required
            />
          </div>
          <div className="col-md-6">
            <Form.Control
              type="text"
              name="asesor"
              placeholder="Asesor"
              value={formData.asesor}
              onChange={handleChange}
              required
            />
          </div>
          <div className="col-md-6">
            <Form.Control
              type="text"
              name="metodoPago"
              placeholder="Método de pago"
              value={formData.metodoPago}
              onChange={handleChange}
              required
            />
          </div>
          <div className="col-md-6">
            <Form.Control
              type="text"
              name="filial"
              placeholder="Filial"
              value={formData.filial}
              onChange={handleChange}
              required
            />
          </div>
          <div className="col-md-6">
            <Form.Control
              type="text"
              name="pais"
              placeholder="País"
              value={formData.pais}
              onChange={handleChange}
              required
            />
          </div>
        </div>

        <h6 className="mt-4 mb-2">Documentos a Entregar</h6>
        <div className="documents-toggle-group">
          <Button
            type="button"
            variant={formData.documentos.certificado1 ? "primary" : "outline-secondary"}
            onClick={() => toggleDocumento("certificado1")}
          >
            📄 Certificado 1
          </Button>
          <Button
            type="button"
            variant={formData.documentos.certificado2 ? "primary" : "outline-secondary"}
            onClick={() => toggleDocumento("certificado2")}
          >
            📄 Certificado 2
          </Button>
          <Button
            type="button"
            variant={formData.documentos.certificado3 ? "primary" : "outline-secondary"}
            onClick={() => toggleDocumento("certificado3")}
          >
            📄 Certificado 3
          </Button>
          <Button
            type="button"
            variant={formData.documentos.certificado4 ? "primary" : "outline-secondary"}
            onClick={() => toggleDocumento("certificado4")}
          >
            📄 Certificado 4
          </Button>
          <Button
            type="button"
            className="w-100 mt-2"
            variant={formData.documentos.diploma ? "primary" : "outline-secondary"}
            onClick={() => toggleDocumento("diploma")}
          >
            🏅 Diploma
          </Button>
        </div>

        <Button type="submit" className="btn btn-primary-custom w-100 mt-auto">
          <i className="bi bi-plus-circle me-2"></i>Agregar Venta
        </Button>
      </Form>
    </aside>
  );
}
