import React from "react";
import { Bar, Doughnut, Line } from "react-chartjs-2";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  Title,
  Tooltip,
  Legend,
  ArcElement,
} from "chart.js";

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  BarElement,
  Title,
  Tooltip,
  Legend,
  ArcElement
);

export default function DashboardCharts({ ventas }) {
  // 🔹 Agrupar por curso
  const cursos = {};
  ventas.forEach((v) => {
    cursos[v.curso] = (cursos[v.curso] || 0) + 1;
  });

  // 🔹 Agrupar por asesor
  const asesores = {};
  ventas.forEach((v) => {
    asesores[v.asesor] = (asesores[v.asesor] || 0) + 1;
  });

  // 🔹 Agrupar por mes (año actual)
  const meses = Array(12).fill(0);
  ventas.forEach((v) => {
    const fecha = new Date(v.fechaISO);
    if (fecha.getFullYear() === new Date().getFullYear()) {
      meses[fecha.getMonth()]++;
    }
  });
  const labelsMeses = ["Ene","Feb","Mar","Abr","May","Jun","Jul","Ago","Sep","Oct","Nov","Dic"];

  return (
    <div className="analytics-grid">
      {/* Cursos más vendidos */}
      <div className="card card-chart">
        <h6>Cursos más vendidos</h6>
        <Doughnut
          data={{
            labels: Object.keys(cursos),
            datasets: [
              {
                data: Object.values(cursos),
                backgroundColor: ["#36a2eb", "#ff6384", "#ffcd56", "#4bc0c0", "#9966ff"],
              },
            ],
          }}
        />
      </div>

      {/* Ventas por asesor */}
      <div className="card card-chart">
        <h6>Ventas por Asesor</h6>
        <Bar
          data={{
            labels: Object.keys(asesores),
            datasets: [
              {
                label: "Ventas",
                data: Object.values(asesores),
                backgroundColor: "#36a2eb",
              },
            ],
          }}
        />
      </div>

      {/* Ventas por mes */}
      <div className="card card-chart">
        <h6>Ventas por Mes</h6>
        <Line
          data={{
            labels: labelsMeses,
            datasets: [
              {
                label: "Ventas",
                data: meses,
                borderColor: "#ff6384",
                backgroundColor: "rgba(255,99,132,0.3)",
              },
            ],
          }}
        />
      </div>
    </div>
  );
}
