import React from "react";
import { Modal, Button, Form } from "react-bootstrap";
import { useVentas } from "../context/VentasContext";

export default function EditarVentaModal() {
  const { ventas, ventaEditando, setVentaEditando, editarVenta } = useVentas();

  // 🔹 Validar venta activa
  const venta =
    ventaEditando !== null && ventas[ventaEditando]
      ? ventas[ventaEditando]
      : null;

  if (!venta) {
    return null;
  }

  const handleChange = (e) => {
    const { name, value } = e.target;
    const nuevasVentas = [...ventas];
    nuevasVentas[ventaEditando] = {
      ...nuevasVentas[ventaEditando],
      [name]: value,
    };
    editarVenta(ventaEditando, nuevasVentas[ventaEditando]);
  };

  const toggleDocumento = (doc) => {
    const nuevasVentas = [...ventas];
    nuevasVentas[ventaEditando] = {
      ...nuevasVentas[ventaEditando],
      documentos: {
        ...nuevasVentas[ventaEditando].documentos,
        [doc]: !nuevasVentas[ventaEditando].documentos?.[doc],
      },
    };
    editarVenta(ventaEditando, nuevasVentas[ventaEditando]);
  };

  const handleClose = () => setVentaEditando(null);

  return (
    <Modal show={ventaEditando !== null} onHide={handleClose} size="lg">
      <Modal.Header closeButton>
        <Modal.Title>Editar Venta</Modal.Title>
      </Modal.Header>

      <Modal.Body>
        <Form>
          {/* 🔹 Datos del cliente */}
          <Form.Group className="mb-3">
            <Form.Label>Nombres completos</Form.Label>
            <Form.Control
              type="text"
              name="nombres"
              value={venta.nombres}
              onChange={handleChange}
            />
          </Form.Group>

          <Form.Group className="mb-3">
            <Form.Label>Correo electrónico</Form.Label>
            <Form.Control
              type="email"
              name="correo"
              value={venta.correo || ""}
              onChange={handleChange}
            />
          </Form.Group>

          <Form.Group className="mb-3">
            <Form.Label>Número de Documento</Form.Label>
            <Form.Control
              type="text"
              name="dni"
              value={venta.dni}
              onChange={handleChange}
            />
          </Form.Group>

          {/* 🔹 Detalles de la venta */}
          <Form.Group className="mb-3">
            <Form.Label>Curso</Form.Label>
            <Form.Control
              type="text"
              name="curso"
              value={venta.curso}
              onChange={handleChange}
            />
          </Form.Group>

          <Form.Group className="mb-3">
            <Form.Label>Asesor</Form.Label>
            <Form.Control
              type="text"
              name="asesor"
              value={venta.asesor}
              onChange={handleChange}
            />
          </Form.Group>

          <Form.Group className="mb-3">
            <Form.Label>Total</Form.Label>
            <Form.Control
              type="number"
              name="total"
              value={venta.total}
              onChange={handleChange}
            />
          </Form.Group>

          {/* 🔹 Documentos */}
          <div className="section-card mb-3">
            <h6 className="text-primary mb-3 text-center">
              <i className="bi bi-folder-check me-2"></i> Documentos a Entregar
            </h6>
            <div className="documents-toggle-group">
              {["certificado1", "certificado2", "certificado3", "certificado4", "diploma"].map(
                (doc) => (
                  <button
                    key={doc}
                    type="button"
                    className={`btn btn-outline-secondary ${
                      venta.documentos?.[doc] ? "active" : ""
                    }`}
                    onClick={() => toggleDocumento(doc)}
                  >
                    {doc.startsWith("certificado") ? "📄 " + doc.replace("certificado", "Certificado ") : "🏅 Diploma"}
                  </button>
                )
              )}
            </div>
          </div>
        </Form>
      </Modal.Body>

      <Modal.Footer>
        <Button variant="secondary" onClick={handleClose}>
          Cancelar
        </Button>
        <Button variant="primary" onClick={handleClose}>
          Guardar Cambios
        </Button>
      </Modal.Footer>
    </Modal>
  );
}
