import React from "react";
import { useVentas } from "../context/VentasContext";

export default function MainContent() {
  const { ventas, resumen, enviarVenta, eliminarVenta, setVentaEditando } = useVentas();

  return (
    <main className="main-content p-4">
      <header className="d-flex justify-content-between align-items-center mb-4">
        <h1 className="h3 mb-0"><i className="bi bi-speedometer2 me-2"></i>Dashboard de Ventas</h1>
        <button className="btn btn-secondary" data-bs-toggle="modal" data-bs-target="#modalVentas">
          <i className="bi bi-table me-2"></i>Ver Todas las Ventas
        </button>
      </header>

      <div className="row g-4 mb-5">
        <div className="col-md-4">
          <div className="metric-card bg-primary-light">
            <i className="bi bi-graph-up-arrow icon-large text-primary"></i>
            <div className="metric-info">
              <span className="metric-value">{resumen.total}</span>
              <span className="metric-label">Ventas Totales</span>
            </div>
          </div>
        </div>
        <div className="col-md-4">
          <div className="metric-card bg-danger-light">
            <i className="bi bi-x-octagon icon-large text-danger"></i>
            <div className="metric-info">
              <span className="metric-value">{resumen.incompletas}</span>
              <span className="metric-label">Ventas Incompletas</span>
            </div>
          </div>
        </div>
        <div className="col-md-4">
          <div className="metric-card bg-warning-light">
            <i className="bi bi-box-seam icon-large text-warning"></i>
            <div className="metric-info">
              <span className="metric-value">{resumen.pendientes}</span>
              <span className="metric-label">Pendientes de Envío</span>
            </div>
          </div>
        </div>
      </div>

      <div className="card p-4">
        <h4 className="mb-3">Últimas Ventas Registradas</h4>
        <div className="table-responsive">
          <table className="table table-hover table-striped">
            <thead>
              <tr>
                <th>Número</th>
                <th>Documento</th>
                <th>Nombres</th>
                <th>Curso</th>
                <th>Asesor</th>
                <th>Fecha</th>
                <th>Acciones</th>
              </tr>
            </thead>
            <tbody>
              {ventas.map((v, i) => (
                <tr key={i}>
                  <td>{v.numeroVenta}</td>
                  <td>{v.dni}</td>
                  <td>{v.nombres}</td>
                  <td>{v.curso}</td>
                  <td>{v.asesor}</td>
                  <td>{new Date(v.fechaISO).toLocaleTimeString()}</td>
                  <td className="d-flex gap-2">
                    {v.estado === "Pendiente" && <button onClick={() => enviarVenta(i)} className="btn-enviar btn btn-sm">Enviar</button>}
                    {v.estado === "Enviado" && <button className="btn btn-sm btn-secondary" disabled>Enviado</button>}
                    {v.estado === "Incompleta" && <button className="btn-completar btn btn-sm" data-bs-toggle="modal" data-bs-target="#modalEditarVenta" onClick={() => setVentaEditando(i)}>Completar</button>}
                    <button className="btn-editar btn btn-sm" data-bs-toggle="modal" data-bs-target="#modalEditarVenta" onClick={() => setVentaEditando(i)}>Editar</button>
                    <button onClick={() => eliminarVenta(i)} className="btn-eliminar btn btn-sm">🗑</button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </main>
  );
}
