import React, { createContext, useContext, useState, useEffect } from "react";

const VentasContext = createContext();
export const useVentas = () => useContext(VentasContext);

export const VentasProvider = ({ children }) => {
  const [ventas, setVentas] = useState([]);
  const [ventaEditando, setVentaEditando] = useState(null);

  // Cargar de localStorage
  useEffect(() => {
    const data = JSON.parse(localStorage.getItem("ventas")) || [];
    setVentas(data);
  }, []);

  // Guardar en localStorage
  useEffect(() => {
    localStorage.setItem("ventas", JSON.stringify(ventas));
  }, [ventas]);

  // Agregar
  const agregarVenta = (venta) => {
    setVentas([...ventas, { ...venta, fechaISO: new Date().toISOString(), estado: "Pendiente" }]);
  };

  // Editar
  const editarVenta = (index, nuevaVenta) => {
    const updated = [...ventas];
    updated[index] = { ...updated[index], ...nuevaVenta, estado: "Pendiente" };
    setVentas(updated);
  };

  // Eliminar
 const eliminarVenta = (index) => {
  setVentas((prev) => {
    const nuevas = [...prev];
    nuevas.splice(index, 1);
    return nuevas;
  });

  setVentaEditando(null); // 👈 resetea modal al eliminar
};

  // Enviar
  const enviarVenta = (index) => {
    const updated = [...ventas];
    const v = updated[index];
    const completa = v.numeroVenta && v.dni && v.nombres && v.curso && v.metodoPago && v.asesor && v.filial && v.pais && v.total;
    updated[index].estado = completa ? "Enviado" : "Incompleta";
    setVentas(updated);
  };

  // Resumen
  const resumen = {
    total: ventas.length,
    incompletas: ventas.filter(v => v.estado === "Incompleta").length,
    pendientes: ventas.filter(v => v.estado === "Pendiente").length,
  };

  return (
    <VentasContext.Provider value={{
      ventas, agregarVenta, editarVenta, eliminarVenta, enviarVenta,
      resumen, ventaEditando, setVentaEditando
    }}>
      {children}
    </VentasContext.Provider>
  );
};
